*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.students-page,h1{
  text-align: center;
  margin-top: 30px;
}
.inputs-div{
  display: flex;
  flex-direction: column;
}
.inputs-div input {
  width: 100%;
  height: 40px;
  margin-top: 10px;
}
input{
  padding-left: 10px;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

input[type="number"] {
  -moz-appearance: textfield; /* Firefox */
}
button{
  width: 120px;
}
.btnn{
  float: right;
}
.card-box{
  border: 1px solid grey;
  width: 100%;
}
h6{
  padding-left: 20px;
  text-align: start;
}
h5{
  padding-left: 20px;
  text-align: start;
}